import Button from '@mui/material/Button';
import logo from '../../assets/logo.png'
import { Box } from '@mui/system';
import Stack from '@mui/material/Stack';
import img2 from '../../assets/imgs/304.png'
import img3 from '../../assets/imgs/305.png'
import car from '../../assets/imgs/carro.png'
import backgroundSemiFooter from '../../assets/imgs/backgroundHome.jpg'
import img1 from '../../assets/fotosOrcamento1.png'
import img2SemiFooter from '../../assets/imgs/autoserv2.jpg'
import img3SemiFooter from '../../assets/imgs/autoserv3.jpg'
import img4SemiFooter from '../../assets/imgs/autoserv4.jpg'
import { Header, Menu, Card, CardHeader, CardBody, Box as Bx, Text, TextInput, FileInput, TextArea, Button as Btn, ResponsiveContext, Select, DateInput } from 'grommet';
import { Link } from "react-router-dom";
import Polirimg from '../../assets/imgs/polimg.jpg'
import martelinhoimg from '../../assets/imgs/martelinhoservices.jpg'
import funilariaimg from '../../assets/imgs/funilariaimg.jpg'
import higenizacaoimg from '../../assets/imgs/higenizacaoimg.jpg'
import vidroimg from '../../assets/imgs/vidroimg.jpg'
import rodaimg from '../../assets/imgs/rodaimg.jpg'
import api from '../../services/api';
import React from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import Drawer from '@mui/material/Drawer';
import { FeedInstagram3Row } from '../../Components/feed/feedInstagram';
import Swal from 'sweetalert2'
import axios from 'axios';

const Orcamento = () => {
    const style = {
        navbarAtual: {
            backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh',
            fontFamily: 'Poppins',

            fontSize: 15, whiteSpace: 'nowrap'
        },
        navbarSecundario: {
            backgroundColor: 'transparent', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh',
            fontFamily: 'Poppins',
            fontSize: 5, whiteSpace: 'nowrap'
        },
        navbarAtualTelefone: { backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'PricedownBl', fontSize: 30, width: '100%' },
        navbarTelefone: { backgroundColor: 'gray', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'PricedownBl', fontSize: 30, width: '100%' },
        form: { width: '30%' },
        formSmall: { width: '80%', marginBottom: '10%' }
    }

    const [unome, setUnome] = React.useState('');
    const [usobrenome, setUsobrenome] = React.useState('');
    const [utelefone, setUtelefone] = React.useState('');
    const [uemail, setUemail] = React.useState('');
    const [uProduto, setUProduto] = React.useState('');
    const [uQuantidade, setUQuantidade] = React.useState('');
    const [uArte, setUArte] = React.useState('');
    const [uDate, setUDate] = React.useState('');
    const [uTamanho, setUTamanho] = React.useState('');
    const [uLargura, setULargura] = React.useState('');
    const [uMaterial, setUMaterial] = React.useState('');
    const [uEsmalte, setUEsmalte] = React.useState('');
    const [uCor, setUCor] = React.useState('');
    const [u3d, setU3d] = React.useState('');
    const [uBorda, setUBorda] = React.useState('');
    const [uGravado, setUGravado] = React.useState('');
    const [uEmbalagem, setUEmbalagem] = React.useState('');
    const [obs, setObs] = React.useState('');

    const [orcamentos, setOrcamentos] = React.useState(null);

    const [uEndereco_cep, setUEnderecoCep] = React.useState('');
    const [uEndereco_rua, setUEnderecoRua] = React.useState('');
    const [uEndereco_numero, setUEnderecoNumero] = React.useState('');
    const [uEndereco_complemento, setUEnderecoComplemento] = React.useState('');
    const [uEndereco_bairro, setUEnderecoBairro] = React.useState('');
    const [uEndereco_cidade, setUEnderecoCidade] = React.useState('');
    const [uEndereco_estado, setUEnderecoEstado] = React.useState('');




    const [foto1, setFoto1] = React.useState(null);
    const [foto2, setFoto2] = React.useState(null);
    const [foto3, setFoto3] = React.useState(null);
    const [foto4, setFoto4] = React.useState(null);

    const handleInfos = (refresh) => {

        let fd = new FormData()
        fd.append("frente", foto1);
        fd.append("verso", foto2);

        var datatime = new Date();
        var dia = String(datatime.getDate()).padStart(2, '0');
        var mes = String(datatime.getMonth() + 1).padStart(2, '0');
        var ano = String(datatime.getFullYear());
        var fecha = dia + '/' + mes + '/' + ano;

        var anoE = uDate.substr(0, 4);
        var mesE = uDate.substr(5, 2);
        var diaE = uDate.substr(8, 2);
        var fechaE = diaE + '/' + mesE + '/' + anoE;


        let data = {
            nome: unome,
            sobrenome: usobrenome,
            telefone: utelefone,
            email: uemail,
            produto: uProduto,
            quantidade: uQuantidade,
            arte: uArte,
            tamanho: uTamanho,
            largura: uLargura,
            esmalte: uEsmalte,
            material: uMaterial,
            cor: uCor,
            m3d: u3d,
            borda: uBorda,
            gravado: uGravado,
            embalagem: uEmbalagem,
            data: fecha,
            dataEntrega: fechaE,
            obs: obs,
            enderecoEntrega_cep: uEndereco_cep,
            enderecoEntrega_logradouro: uEndereco_rua,
            enderecoEntrega_numero: uEndereco_numero,
            enderecoEntrega_completomento: uEndereco_complemento,
            enderecoEntrega_bairro: uEndereco_complemento,
            enderecoEntrega_estado: uEndereco_estado,
            enderecoEntrega_cidade: uEndereco_estado,
            idcot: orcamentos,

        }

        if (orcamentos !== '') {
            if (foto1 !== null) {
                api.post('enviar-informacoes', data)
                    .then((res) => {
                        console.log(res.data)

                        api.post('enviar-informacoes-foto/' + res.data, fd)
                            .then(res => {
                                alert('pedido de orcamento enviado')
                                console.log(res.data)
                            })
                    })
                    .catch(e => {
                        alert('falha ao pedir oramento com foto');
                        console.log(e)
                    }
                    )
            }
            else {

                api.post('enviar-informacoes', data)
                    .then((res) => {

                        console.clear();
                        console.table(data);
                        console.table(res.data)

                        if (refresh) {
                            Swal.fire(
                                'Deleted!',
                                'Your file has been deleted.',
                                'success'
                            ).then(() => {
                                window.location.reload();
                            })
                        }
                        else {
                            setOrcamentos(res.data);
                            clearHalf();
                        }
                    })
                    .catch(e => {
                        Swal.fire({
                            icon: 'error',
                            title: 'Oops...',
                            text: 'Something went wrong!',
                            footer: '<a href="">Why do I have this issue?</a>'
                        })
                        console.log(e)
                    }
                    )
            }
        }
        else {

        }

        return 1;


    }

    const OpenModalOption = () => {
        Swal.fire({
            title: 'Deseja adicionar mais um oramento?',
            // text: "You won't be able to revert this!",
            icon: 'question',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            cancelButtonText: 'No',
            confirmButtonText: 'Sim'
        }).then((result) => {
            if (result.isConfirmed) {
                handleInfos(false);
            }
            else {
                handleInfos(true);
            }
        })
    }

    const clearHalf = async () => {
        setClean(true);
        setFoto1(null);
        setFoto2(null);
        setUQuantidade('');
        setUTamanho('');
        setULargura('');
        // setUEndereco('');
        setUMaterial('');
        setUEsmalte('');
        setUCor('');
        setU3d('');
        setUBorda('');
        setUGravado('');
        setObs('');
        await setUEmbalagem('');
        setClean(false);
    }
    const size = React.useContext(ResponsiveContext);
    const [sidebar, setSidebar] = React.useState(false);

    const [clenaing, setClean] = React.useState(false);

    const PesquisarCep = (valor) => {
        var cep = valor.replace(/\D/g, '');

        if (cep != "") {
            var validacep = /^[0-9]{8}$/;
            if (validacep.test(cep)) {
                axios.get('https://viacep.com.br/ws/' + cep + '/json').then(res => {
                    console.log(res.data)
                    setUEnderecoCep(res.data.cep);
                    setUEnderecoRua(res.data.logradouro);
                    setUEnderecoBairro(res.data.bairro);
                    setUEnderecoCidade(res.data.localidade);
                    setUEnderecoEstado(res.data.uf);
                })
            } //end if.
            else {
                alert("Formato de CEP invlido.");
            }
        }
    };

    return (
        <>
            <Header pad={{ left: 'xlarge', right: 'xlarge' }}
            // background={{ color: 'rgb(179 179 179)' }}
            >
                <Stack
                    justifyContent="center"
                    alignItems="center"
                    spacing={2}>
                    <img src={logo} style={{ height: '10vh' }} />

                </Stack>

                {size !== 'small' ? (<Stack direction="row" mt={2} spacing={2} sx={{ position: 'relative' }}>
                    {/* <Link to="/" style={{ textDecoration: 'none' }}> */}
                    <Button variant="contained" sx={style.navbarSecundario}> <Text color={'black'}> inicio </Text></Button>
                    {/* </Link> */}
                    {/* <Link to="/sobrenos" style={{ textDecoration: 'none' }}> */}
                    <Button variant="contained" sx={style.navbarSecundario}><Text color={'black'}> Chilecoins </Text></Button>
                    {/* </Link> */}
                    {/* <Link to="/servicos" style={{ textDecoration: 'none' }}> */}
                    <Button variant="contained" sx={style.navbarSecundario}><Text color={'black'}> Servios </Text></Button>
                    {/* </Link> */}
                    {/* <Link to="/contato" style={{ textDecoration: 'none' }}> */}
                    <Button variant="contained" sx={style.navbarSecundario} ><Text color={'black'}> Portifolio </Text></Button>
                    {/* </Link> */}
                    {/* <Link to="/orcamento" style={{ textDecoration: 'none' }}> */}
                    <Button variant="contained" sx={style.navbarSecundario}><Text color={'black'}> News </Text></Button>
                    {/* </Link> */}
                    {/* <Link to="/orcamento" style={{ textDecoration: 'none' }}> */}
                    <Button variant="contained" sx={style.navbarSecundario}><Text color={'black'}> FALE CONOSCO </Text></Button>
                    {/* </Link> */}
                </Stack>) : (
                    <>
                        <MenuIcon onClick={() => setSidebar(true)} />
                        <Drawer
                            anchor='right'
                            open={sidebar}
                        // onClose={setSidebar(false)}
                        >
                            <Stack direction="column" mt={2} spacing={2} sx={{ position: 'relative' }}>

                                {/* <Link to="/" style={{ textDecoration: 'none' }}> */}
                                <Button variant="contained" sx={style.navbarTelefone}>inicio</Button>
                                {/* </Link> */}
                                {/* <Link to="/sobrenos" style={{ textDecoration: 'none' }}> */}
                                <Button variant="contained" sx={style.navbarTelefone}>Chilecoins</Button>
                                {/* </Link> */}
                                {/* <Link to="/servicos" style={{ textDecoration: 'none' }}> */}
                                <Button variant="contained" sx={style.navbarTelefone}>Servios</Button>
                                {/* </Link> */}
                                {/* <Link to="/contato" style={{ textDecoration: 'none' }}> */}
                                <Button variant="contained" sx={style.navbarTelefone} >Portifolio</Button>
                                {/* </Link> */}
                                {/* <Link to="/orcamento" style={{ textDecoration: 'none' }}> */}
                                <Button variant="contained" sx={style.navbarTelefone}>News</Button>
                                {/* </Link> */}
                                {/* <Link to="/orcamento" style={{ textDecoration: 'none' }}> */}
                                <Button variant="contained" sx={style.navbarTelefone}>FALE CONOSCO</Button>
                            </Stack>
                        </Drawer>
                    </>)}

            </Header>


            <Stack direction="row"
                justifyContent="center"
                alignItems="center"
                spacing={15}
            >
                <Box width={'30%'}>
                    <h1>
                        Oramento de moeda personalizada
                    </h1>
                    <span>
                        Siga o passo a passo para a construo da sua moeda, preencha todos os dados e no final envie para a gente
                    </span>

                </Box>


                <Bx
                // border={{ color: '#F7A600', size: 'medium' }}
                >
                    <img src={img1} style={{ width: '400px', height: '200px' }} />


                </Bx>
            </Stack>

            <Stack
                justifyContent="center"
                alignItems="center">

                <Bx style={size !== 'small' ? style.form : style.formSmall} gap='medium'>
                    <Text style={{ fontFamily: 'Poppins', fontSize: 30, color: '#F7A600' }}>Dados Gerais</Text>

                    <Bx direction='row' style={{ width: '100%' }} gap='small'>
                        <TextInput
                            value={unome}
                            onChange={event => setUnome(event.target.value)}
                            placeholder="Nome"

                        />
                        <TextInput
                            value={usobrenome}
                            placeholder="Sobrenome"
                            onChange={event => setUsobrenome(event.target.value)}

                        />
                    </Bx>

                    <TextInput value={utelefone} onChange={event => setUtelefone(event.target.value)} placeholder="Telefone" />

                    <TextInput value={uemail} onChange={event => setUemail(event.target.value)} placeholder="Email" />

                    <Select placeholder="Produto" options={['Moeda', 'Medalha']} onChange={({ option }) => setUProduto(option)} />


                    <TextInput placeholder='Quantidade - minimo 50' type='number' onChange={(event) => setUQuantidade(event.target.value)} value={uQuantidade} />

                    <Select placeholder="Voc j tem a arte do projeto?" options={['Sim, tenho a arte', 'No, no tenho a arte']} onChange={({ option }) => setUArte(option)} />

                    {clenaing ? (<></>) : (

                        <FileInput messages={{
                            browse: "Anexar",
                            dropPrompt: "Se tiver a arte, envie-nos o seu projeto - frente",
                            files: "files",
                            remove: "remover",
                        }}
                            accept="image/png, image/gif, image/jpeg"
                            id='fotos'
                            name="foto-obrigatoria"

                            onChange={(event, { files }) => {
                                const fileList = files;
                                for (let i = 0; i < fileList.length; i += 1) {
                                    const file = fileList[i];
                                    setFoto1(file);


                                }
                            }}
                        />


                    )}

                    {clenaing ? (<></>) : (


                        <FileInput messages={{
                            browse: "Anexar",
                            dropPrompt: "Se tiver a arte, envie-nos o seu projeto - verso",
                            files: "files",
                            remove: "remover",
                        }}
                            accept="image/png, image/gif, image/jpeg"
                            id='fotos'
                            name="foto-obrigatoria"

                            onChange={(event, { files }) => {
                                const fileList = files;
                                for (let i = 0; i < fileList.length; i += 1) {
                                    const file = fileList[i];
                                    setFoto2(file);
                                }
                            }}
                        />

                    )}

                    <DateInput format="dd/mm/yyyy" placeholder='Para quando voc precisa?' onChange={({ value }) => { setUDate(value) }} />

                    <Select placeholder="Diametro" options={['4 cm', '5 cm', '5,5 cm', '6 cm', '6,5 cm', '8,0 cm']} onChange={({ option }) => setUTamanho(option)} value={uTamanho} />

                    <Select options={['2 mm', '3 mm', '4 mm', '5 mm', '6 mm']} placeholder="Espessura" onChange={({ option }) => setULargura(option)} value={uLargura} />

                    <Select placeholder="Material" options={['Bronze', 'Zinco']} onChange={({ option }) => setUMaterial(option)} value={uMaterial} />

                    <Select
                        placeholder="Esmalte"
                        options=
                        {
                            ['Ouro Brillante - Esmalte simple',
                                'Ouro Brillante - Esmalte duplo',
                                'Ouro envelhecido - Esmalte Simple',
                                'Ouro envelhecido - Esmalte duplo',
                                'Prata Envelhecido - Esmalte Simple',
                                'Prata Envelhecido - Esmalte Duplo',
                                'Bronze - Esmalte Simple',
                                'Bronze - Esmalte Duplo',
                                'Bronze envelhecido - Esmalte Simple',
                                'Bronze envelhecido - Esmalte Duplo',
                                'Cobre - Esmalte Simple',
                                'Cobre - Esmalte Duplo',
                            ]
                        }
                        // value={value}
                        onChange={({ option }) => setUEsmalte(option)}
                        value={uEsmalte}
                    />
                    <Select placeholder="Cor" options={['Cor - um lado', 'Cor - os dois lados']} onChange={({ option }) => setUCor(option)} value={uCor} />

                    <Select placeholder="3D" options={['3D - um lado', '3D - os dois lados']} onChange={({ option }) => setU3d(option)} value={u3d} />

                    <Select placeholder="Borda" options={['Borde Especial']} onChange={({ option }) => setUBorda(option)} value={uBorda} />

                    <Select placeholder="Gravado" options={['Gravado Laser costado']} onChange={({ option }) => setUGravado(option)} value={uGravado} />

                    <Select placeholder="Embalagem" options={['Sacola PVC', 'Caixa Acrlico', 'Caixa Veludo']} onChange={({ option }) => setUEmbalagem(option)} value={uEmbalagem} />

                    <TextArea
                        placeholder="Observaes"
                        value={obs}
                        onChange={event => setObs(event.target.value)}
                    />

                    <TextInput placeholder='Cep'
                        onChange={event => setUEnderecoCep(event.target.value)}
                        onBlur={(e) => PesquisarCep(e.target.value)}
                        value={uEndereco_cep}
                    />

                    <TextInput placeholder='Logradouro'
                        onChange={event => setUEnderecoRua(event.target.value)}
                        value={uEndereco_rua}
                    />

                    <TextInput placeholder='Nmero'
                        onChange={event => setUEnderecoNumero(event.target.value)}
                        value={uEndereco_numero}
                    />

                    <TextInput placeholder='Complemento'
                        onChange={event => setUEnderecoComplemento(event.target.value)}
                        value={uEndereco_complemento}
                    />

                    <TextInput placeholder='Bairro'
                        onChange={event => setUEnderecoBairro(event.target.value)}
                        value={uEndereco_bairro}
                    />

                    <TextInput placeholder='Cidade'
                        onChange={event => setUEnderecoCidade(event.target.value)}
                        value={uEndereco_cidade}
                        disabled
                    />

                    <TextInput placeholder='Estado'
                        onChange={event => setUEnderecoEstado(event.target.value)}
                        value={uEndereco_estado}
                        disabled
                    />



                    <Btn primary label="Enviar" style={{ backgroundColor: '#F3C266', border: 'none', marginBottom: '10%' }} onClick={() => {


                        OpenModalOption();



                    }} color='black' />
                </Bx>

                <FeedInstagram3Row />
            </Stack>

        </>
    );
}

export default Orcamento;
