
import logo from '../../assets/logo.png'
import React from 'react';
import { Header, Box as Bx, Text, TextInput, Button as Btn, Card, DataTable, Meter, Button, Table, TableHeader, TableBody, TableRow, TableCell, ResponsiveContext, Tab, Tabs, FileInput } from 'grommet';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Bt from '@mui/material/Button';
import api from '../../services/api'
import { red } from '@mui/material/colors';

import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const Login = () => {

    const [logado, setLogado] = React.useState(false);
    const [orcando, setOrcando] = React.useState(false);
    const [user, setUser] = React.useState([]);

    const [DadosGlobal, setDG] = React.useState([]);

    const [unome, setUnome] = React.useState('');
    const [usobrenome, setUsobrenome] = React.useState('');
    const [utelefone, setUtelefone] = React.useState('');
    const [uemail, setUemail] = React.useState('');
    const [uano, setUano] = React.useState('');

    const [total, setTotal] = React.useState(0);

    const [dadosTable, setDadosTable] = React.useState([]);
    const [dadosTableEnviados, setDadosTableEnviados] = React.useState([]);

    const [idOrc, setIdorc] = React.useState(null);
    const [idCotacao, setIdCotacao] = React.useState(null);

    const handleSendOrcament = () => {
        var datatime = new Date();
        var dia = String(datatime.getDate()).padStart(2, '0');
        var mes = String(datatime.getMonth() + 1).padStart(2, '0');
        var ano = String(datatime.getFullYear());
        var fecha = dia + '/' + mes + '/' + ano;

        let data = {
            nome: unome,
            sobrenome: usobrenome,
            email: uemail,
            ano: uano,
            valor_total: total,
            data: fecha,
            id_funcionario: user.id,
            nome_funcionario: user.nome,
            idOrc: idOrc,
            cotacao: idCotacao,
            desconto: desconto,
        }

        console.clear();

        api.post('/enviar-orcamento', data).then((res) => {
            console.log('oi');
            console.table(res.data)
            alert('Orcamento enviado');
            // clearDados();
        }).catch(error => {
            alert('Erro ao enviar oramento')
        })
    }

    const style = {
        navbarAtual: { backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'Poppins' },
        navbarSecundario: { backgroundColor: 'transparent', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'Poppins' },
        card: {
            left: '30%',
            right: '30%',
            top: 'xlarge'
        },
        cardSmall: {
            left: '2%',
            right: '2%',
            top: 'xlarge'
        },
        table: { "borderCollapse": "collapse", "width": "100%" },
        th: { "border": "1px solid #dddddd", "textAlign": "left", "padding": "8px", "fontSize": "13px", backgroundColor: '#dddddd' },

        // cardsServices: { }
    }

    const handleLogin = () => {
        let emailinput = document.querySelector("#email")
        let passwordinput = document.querySelector("#password")
        let data = {
            email: emailinput.value,
            password: passwordinput.value
        }
        api.post('/login', data).then((res) => {

            localStorage.setItem('token', JSON.stringify(res.data.access_token));

            api.defaults.headers.Authorization = `Bearer ${res.data.access_token}`;

            api.post('/me').then((res) => {
                if (res.data.message === 'sucess') {
                    setUser(res.data.usuario.original);
                    console.log(res.data);

                    api.get('listar-informacoes-orcamentos')
                        .then(res => {
                            console.log(res.data);

                            const PendetesFiltrados = res.data.pendetes
                                .sort(({ seq: a }, { seq: b }) => b - a)
                                .filter(({ id_cotacao }, i) => i === res.data.pendetes.findIndex(({ id_cotacao: _id }) => id_cotacao === _id));


                            setDadosTable(PendetesFiltrados);
                            setDadosTableEnviados(res.data.enviados);
                            setLogado(true);
                        })


                }
            })

        })
        // setLogado(true);

    }

    const clearDados = () => {
        setUnome('');
        setUsobrenome('');
        setUtelefone('');
        setUemail('');
        setIdorc('');
        setIdCotacao('');
        setUnome('');
        setUsobrenome('');
        setUtelefone('');
        setUemail('');
        setDesconto(0);
        // setOrcando(false);
    }
    React.useEffect(() => {

        const token = localStorage.getItem('token');
        if (token) {
            api.defaults.headers.Authorization = `Bearer ${JSON.parse(token)}`;

            api.post('/me').then((res) => {
                if (res.data.message === 'sucess') {
                    setUser(res.data.usuario.original);
                    console.log(res.data);
                    api.get('listar-informacoes-orcamentos')
                        .then(res => {
                            console.log(res.data);

                            const PendetesFiltrados = res.data.pendetes
                                .sort(({ seq: a }, { seq: b }) => b - a)
                                .filter(({ id_cotacao }, i) => i === res.data.pendetes.findIndex(({ id_cotacao: _id }) => id_cotacao === _id));


                            setDadosTable(PendetesFiltrados);
                            setDadosTableEnviados(res.data.enviados);
                            setLogado(true);
                        })


                }
            })
        }
    }, [])

    const [dadosTabelaOrcamento, setDtb] = React.useState([]);

    const handleSelectOrcamento = async dadosOrc => {
        api.get('/calcular-frete/' + dadosOrc.id_cotacao).then(res => {
            setVlrCor(res.data);
            setLoading(false)
        })

        // console.log(dadosOrc.);
        await setDG(dadosOrc);

        await setIdorc(dadosOrc.id);
        await setIdCotacao(dadosOrc.id_cotacao);
        await setUnome(dadosOrc.nome);
        await setUsobrenome(dadosOrc.sobrenome);
        await setUtelefone(dadosOrc.telefone);
        await setUemail(dadosOrc.email);

        let resp = await api.get('/getOrcamento/' + dadosOrc.id_cotacao).then(res => {
            var totalLocal = dadosTabelaOrcamento.reduce(getTotal, 0);

            function getTotal(totalLocal, item) {
                return total + (item.valor_total);
            }

            setTotal(totalLocal);
            console.clear();
            console.log('dtb');
            console.log(res.data);
            setDtb(res.data);

        })

        setOrcando(true);
    }

    const size = React.useContext(ResponsiveContext);
    const [sidebar, setSidebar] = React.useState(false);

    const [open, setOpen] = React.useState(false);
    const [openImages, setModalImages] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const Modalstyle = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    };

    const ModalstyleImage = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 600,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    };
    const [novoNome, setNovoNome] = React.useState('');
    const [novoEmail, setNovoEmail] = React.useState('');
    const [novoSenha, setNovoSenha] = React.useState('');

    const CriarUsaurio = () => {
        api.post('/user', { nome: novoNome, email: novoEmail, password: novoSenha }).then(res => {
            alert('Usuario criado');
            setNovoEmail('');
            setNovoNome('');
            setNovoSenha('');
            handleClose();

        }
        ).catch(error => {
            alert('Dados invalidos');
        })
    }

    const Pendetes = () => {
        return <DataTable
            // size={'small'}
            columns={[
                {
                    property: 'id_cotacao',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>N Cotacao</Text>,
                    primary: true,
                },

                {
                    property: 'data',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Data</Text>,
                    primary: true,
                },
                {
                    property: 'nome',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Nome</Text>,
                    primary: true,
                },
                {
                    property: 'sobrenome',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Sobrenome</Text>,
                    primary: true,
                },
                {
                    property: 'telefone',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Telefone</Text>,
                    primary: true,
                },
                {
                    property: 'email',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Email</Text>,
                    primary: true,
                },
                {
                    property: 'percent',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Opcoes</Text>,
                    render: datum => (
                        <Bx pad={{ vertical: 'xsmall' }}>
                            <Button label='Fazer oramento' style={{ backgroundColor: '#00ed1b', border: 'none' }} onClick={() => { handleSelectOrcamento(datum) }} />
                        </Bx>
                    ),
                },
            ]}
            data={dadosTable}
        />
    }

    const Enviados = () => {
        return <DataTable
            // size={'small'}
            columns={[
                {
                    property: 'id_cotacao',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>N Cotacao</Text>,
                    primary: true,
                },

                {
                    property: 'data',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Data/Hora</Text>,
                    primary: true,
                },
                {
                    property: 'nome',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Nome</Text>,
                    primary: true,
                },
                {
                    property: 'sobrenome',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Sobrenome</Text>,
                    primary: true,
                },
                {
                    property: 'telefone',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Telefone</Text>,
                    primary: true,
                },
                {
                    property: 'email',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Email</Text>,
                    primary: true,
                },
                {
                    property: 'marca',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Marca</Text>,
                    primary: true,
                },
                {
                    property: 'modelo',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Modelo</Text>,
                    primary: true,
                },
                {
                    property: 'ano',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Ano</Text>,
                    primary: true,
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Martelinho</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_martelinho}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Pintura</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_pintura}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Polimento</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_polimento}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Vitrificao</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_vitrificacao}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Rodas</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_rodas}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Higienizao</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_higienizacao}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Outros</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_outros}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Total</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> R$ {datum.valor_total}.00</Text>
                    ),
                },
                {
                    property: 'valor_martelinho',
                    header: <Text style={{ whiteSpace: 'nowrap' }}>Funcinario responsavel</Text>,
                    render: datum => (
                        <Text style={{ whiteSpace: 'nowrap' }}> {datum.nome_funcionario}</Text>
                    ),
                },


                // {
                //     property: 'percent',
                //     header: <Text>Opcoes</Text>,
                //     render: datum => (
                //         <Bx pad={{ vertical: 'xsmall' }}>
                //             <Button label='Fazer oramento' style={{ backgroundColor: '#00ed1b', border: 'none' }} onClick={() => { handleSelectOrcamento(datum) }} />
                //         </Bx>
                //     ),
                // },
            ]}
            data={dadosTableEnviados}
        />
    }

    const CalcularTotal = () => {
        let totalLocal = 0;
        dadosTabelaOrcamento.forEach(dados => {
            totalLocal += dados.valor_total
        });

        return totalLocal;
    }


    const [loadingValores, setLoading] = React.useState(true);
    const [valorCor, setVlrCor] = React.useState(true);
    const [desconto, setDesconto] = React.useState('');

    const CalcularFrete = () => {
        return (<>
            {loadingValores ? (
                <tr>
                    <th style={style.th}>Carregando valores entrega</th>
                    <th style={style.th}>...</th>
                    <th style={style.th}>...</th>
                    <th style={style.th}>...</th>
                    <th style={style.th}>...</th>
                </tr>
            ) :
                (
                    <tr>
                        <th style={style.th}>Valor Sedex</th>
                        <th style={style.th}>{valorCor.TotalSedex.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}</th>
                        <th style={style.th}></th>
                        <th style={style.th}>Valor Pac</th>
                        <th style={style.th}>{valorCor.TotalPac.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}</th>
                    </tr>)}
        </>);

    }


    const TableSpace = () => {
        return (<tr>
            <th style={style.th}></th>
            <th style={style.th}></th>
            <th style={style.th}></th>
            <th style={style.th}></th>
            <th style={style.th}></th>
        </tr>)
    }

    const ModalDeFotos = () => {
        return (
            <Tabs>



                {dadosTabelaOrcamento.map((dados, key) =>
                (<>
                    <Tab title={"Moeda " + (key + 1)}>



                        <Text> Frente:</Text>
                        <Stack
                            direction="row"
                            justifyContent="center"
                            alignItems="center"
                            spacing={4}
                        >
                             {/* {dados.foto_frente
                                ? <img src={'http://127.0.0.1:8000/fotos_orcamento/' + dados.foto_frente} style={{ width: '200px' }} />
                                : <img src={'https://www.goiania.go.leg.br/imagens/sem-foto/image'} style={{ width: '200px' }} />} */}

                            {dados.foto_frente
                                ? <img src={'https://chilecoins.com.br/app/public/fotos_orcamento/' + dados.foto_frente} style={{ width: '200px' }} />
                                : <img src={'https://www.goiania.go.leg.br/imagens/sem-foto/image'} style={{ width: '200px' }} />}

                            <Bx>
                                <FileInput messages={{
                                    browse: "Anexar",
                                    dropPrompt: "Alterar ou anexar um estilo de frente",
                                    files: "files",
                                    remove: "remover",
                                }}
                                    accept="image/png, image/gif, image/jpeg"
                                    id='fotos'
                                    name="foto-obrigatoria"

                                    onChange={(event, { files }) => {
                                        const fileList = files;
                                        for (let i = 0; i < fileList.length; i += 1) {
                                            const file = fileList[i];
                                            console.log(file);
                                            // setFoto2(file);
                                        }
                                    }}
                                />
                            </Bx>
                        </Stack>

                        <Text> Verso:</Text>
                        <Stack
                            direction="row"
                            justifyContent="center"
                            alignItems="center"
                            spacing={4}
                        >
                            {dados.foto_verso
                                ? <img src={'https://chilecoins.com.br/app/public/fotos_orcamento/' + dados.foto_verso} style={{ width: '200px' }} />
                                : <img src={'https://www.goiania.go.leg.br/imagens/sem-foto/image'} style={{ width: '200px' }} />}
                            <Bx>
                                <FileInput messages={{
                                    browse: "Anexar",
                                    dropPrompt: "Alterar ou anexar um estilo de verso",
                                    files: "files",
                                    remove: "remover",
                                }}
                                    accept="image/png, image/gif, image/jpeg"
                                    id='fotos'
                                    name="foto-obrigatoria"

                                    onChange={(event, { files }) => {
                                        const fileList = files;
                                        for (let i = 0; i < fileList.length; i += 1) {
                                            const file = fileList[i];
                                            console.log(file);
                                            // setFoto2(file);
                                        }
                                    }}
                                />
                            </Bx>
                        </Stack>
                    </Tab>
                </>)

                )}
            </Tabs>
        )
    }

    const InformacoesDoCliente = () => {
        return (
            <Bx gap='small'>
                <Text> Dados do cliente</Text>
                <Bx direction='row' style={{ width: '100%' }} gap='small'>
                    <TextInput
                        value={unome}
                        onChange={event => setUnome(event.target.value)}
                        placeholder="Nome"
                    />
                    <TextInput
                        value={usobrenome}
                        placeholder="Sobrenome"
                        onChange={event => setUsobrenome(event.target.value)}
                    />
                </Bx>
                <TextInput
                    value={utelefone}
                    placeholder="Telefone: +55 (ddd)"
                    onChange={event => setUtelefone(event.target.value)}
                />

                <TextInput
                    value={uemail}
                    placeholder="Email"
                    onChange={event => setUemail(event.target.value)}
                />
            </Bx>

        )
    }

    const TableDeDados = () => {
        return (
            <table style={style.table}>
                <tr><th style={style.th}>Id</th>
                    <th style={style.th}>Descrio</th>
                    <th style={style.th}>Valor unitrio</th>
                    <th style={style.th}>Quantidade</th>
                    <th style={style.th}>Total</th>
                </tr>
                {dadosTabelaOrcamento.map((dados, key) =>
                    <tr>
                        <td style={style.th}>{key + 1}</td>
                        <td style={style.th}>{dados.material} {dados.produto} {dados.largura}</td>
                        <td style={style.th}>{dados.valor_unitario.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}</td>
                        <td style={style.th}>{dados.quantidade}</td>
                        <td style={style.th}>{dados.valor_total.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}</td>
                    </tr>
                )}



                <tr>
                    <th style={style.th}></th>
                    <th style={style.th}></th>
                    <th style={style.th}></th>
                    <th style={style.th}>Total</th>
                    <th style={style.th}>{CalcularTotal().toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}</th>
                </tr>

                <TableSpace />

                {/* {CalcularFrete(dadosTabelaOrcamento[0])} */}

                <CalcularFrete />

                <TableSpace />

                <tr>
                    <th style={style.th}>Ver valores unitario</th>
                    <th style={style.th}><Btn primary label="Ver valores" style={{ backgroundColor: '#F3C266', border: 'none' }} color='black' /></th>
                    <th style={style.th}></th>
                    <th style={style.th}></th>
                    <th style={style.th}></th>
                </tr>

                <tr>
                    <th style={style.th}>Definir fotos</th>
                    <th style={style.th}><Btn primary label="Ver fotos" style={{ backgroundColor: '#F3C266', border: 'none' }} color='black' onClick={() => setModalImages(!openImages)} /></th>
                    <th style={style.th}></th>
                    <th style={style.th}></th>
                    <th style={style.th}></th>
                </tr>

            </table>
        )
    }




    return (
        <>
            {logado == false ? (
                <>
                    <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                        <img src={logo} style={{ height: '10vh', alignSelf: 'center' }} />


                    </Header><Card margin={
                        size !== 'small' ? style.card : style.cardSmall}
                        background={{ color: 'rgb(179 179 179)' }}
                        pad='large'
                    >
                        <Bx gap='medium'
                            margin={{
                                left: '20%',
                                right: '20%',

                            }}
                        >
                            <Stack
                                justifyContent="center"
                                alignItems="center"
                            >

                                <img src={logo} />
                            </Stack>
                            <TextField id="email" label="Email" variant="filled" sx={{ backgroundColor: 'white' }} />

                            <TextField id="password" label="Senha" variant="filled" sx={{ backgroundColor: 'white' }} type={'password'} />

                            <Btn primary label="Entrar" style={{ backgroundColor: '#F3C266', border: 'none' }} color='black' onClick={() => handleLogin()} />
                        </Bx>
                    </Card>
                </>) : (
                <>
                    <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                        <img src={logo} style={{ height: '10vh', alignSelf: 'center' }} />

                        <Stack direction="row" mt={2} spacing={2} sx={{ position: 'relative' }}>

                            <Bt variant="contained" sx={style.navbarAtual} onClick={() => setOrcando(true)} >Novo Oramento</Bt>

                            <Bt variant="contained" sx={style.navbarAtual} onClick={() => handleOpen()} >Novo Usurio</Bt>

                            <Bt variant="contained" sx={style.navbarAtual} onClick={() => { setLogado(false); localStorage.clear(); }} >Sair</Bt>

                        </Stack>
                    </Header>

                    <Modal
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="modal-modal-title"
                        aria-describedby="modal-modal-description"
                    >
                        <Box sx={Modalstyle}>
                            <Stack
                                direction="column"
                                justifyContent="center"
                                alignItems="center"
                                spacing={4}
                            >
                                <Text> Novo usurio</Text>
                                <TextInput
                                    value={novoNome}
                                    placeholder="Nome"
                                    onChange={event => setNovoNome(event.target.value)}
                                />

                                <TextInput
                                    value={novoEmail}
                                    placeholder="Email"
                                    onChange={event => setNovoEmail(event.target.value)}
                                />
                                <TextInput
                                    value={novoSenha}
                                    placeholder="Senha"
                                    onChange={event => setNovoSenha(event.target.value)}
                                />
                                <Bt variant="contained" sx={style.navbarAtual} onClick={() => CriarUsaurio()} >Criar Usurio</Bt>
                            </Stack>
                        </Box>
                    </Modal>

                    {orcando == false ? (<>
                        <Tabs>
                            <Tab title="Pendentes">
                                <Pendetes />
                            </Tab>
                            <Tab title="Enviados">
                                <Enviados />
                            </Tab>
                            <Tab title="Clientes">

                            </Tab>
                        </Tabs>

                    </>
                    ) : (<>
                        <Stack justifyContent="center"
                            alignItems="center">


                            <h1>ORAMENTO</h1>


                            <Bx style={{ width: '30%' }} gap='small'>
                                <InformacoesDoCliente />

                                <Modal
                                    open={openImages}
                                    onClose={() => setModalImages(!openImages)}
                                    aria-labelledby="modal-modal-title"
                                    aria-describedby="modal-modal-description"
                                >
                                    <Box sx={ModalstyleImage} >

                                        <Stack
                                            direction="column"
                                            justifyContent="center"
                                            alignItems="center"
                                            spacing={4}
                                        >
                                            <ModalDeFotos />



                                        </Stack>
                                    </Box>
                                </Modal>


                                <TableDeDados />

                                <Stack direction={'column'} justifyContent="left" gap={'20%'} alignItems="center"  >
                                    <Text>Desconto</Text>
                                    <Stack direction={'row'} justifyContent="center" gap={'1%'}
                                        alignItems="center" >
                                        R$  <TextInput
                                            type={'number'}
                                            onChange={event => setDesconto(event.target.value)}
                                        />
                                    </Stack>

                                    <Text>Valor com desconto</Text>
                                    <Stack direction={'row'} justifyContent="center" gap={'1%'}
                                        alignItems="center" >
                                        R$  <TextInput
                                            type={'number'}
                                            value={CalcularTotal() - desconto}
                                        />
                                    </Stack>
                                </Stack>



                                <Btn primary label="Enviar" style={{ backgroundColor: '#F3C266', border: 'none' }} onClick={() => handleSendOrcament()} color='black' />
                                <Btn primary label="Cancelar" style={{ backgroundColor: '#B3B3B3', border: 'none', marginBottom: '5%' }} color='black' onClick={() => {
                                    clearDados();
                                }} />
                            </Bx>
                        </Stack>
                    </>)}




                </>
            )
            }


        </>
    );
}

export default Login;
