
import React from 'react'
import { Box, ResponsiveContext, } from 'grommet';


export const FeedInstagram3Row = () => {

    const size = React.useContext(ResponsiveContext);

    return (
        <>

            <Box direction={size !== 'small' ? ('row') : 'column'} gap='xlarge'>
                <iframe id="frame" width="320" height="568" src="https://www.instagram.com/p/CWMJWmQvULG/embed" frameborder="0"></iframe>

                <iframe id="frame" width="320" height="568" src="https://www.instagram.com/p/CUI_pdSLN6r/embed" frameborder="0"></iframe>

                <iframe id="frame" width="320" height="568" src="https://www.instagram.com/p/CS9oyzmDAH1/embed" frameborder="0"></iframe>
            </Box>

        </>
    );
}


